// API_PATH
import {
  API_PATH
} from '@/config'

// ajax
import ajax from '@/utils/ajax'

/**
 * 月业务量
 * @const
 * @type {String}
 * @default  '[API_PATH]/target/businessForMonth'
 */
export const GET_BUSINESS_FOR_MONTH = API_PATH + '/target/businessForMonth'

/**
 * 月业务量
 * @param {object} [data] 发送键值对数据
 * @returns {promise}
 */
export function getBusinessForMonth(data) {
  return ajax({
    method: 'POST',
    headers: {
      "Content-Type": "application/x-www-form-urlencoded"
    },
    data: data,
    url: GET_BUSINESS_FOR_MONTH
  })
}

